* {
  scroll-behavior: smooth;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-margin-top: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

div, button {
  justify-content: center;
  align-items: center;
  display: flex;
}

p, li, code {
  color: #000000b3;
  font-size: 1rem;
  line-height: 1.5rem;
}

a {
  cursor: pointer;
}

code {
  overflow-wrap: break-word;
  margin: 30px 0 30px 60px;
  font-family: monospace;
  font-size: 1rem;
  display: block;
}

.nav-header, .unit-header {
  font-size: 1.8rem;
}

.nav-container {
  display: unset;
  width: 350px;
  height: 100%;
  z-index: 10;
  transition: all 1s;
  position: fixed;
  top: 0;
  left: 0;
}

.nav-container #navbar, .nav-container #slider-tab {
  position: absolute;
}

.nav-container #navbar {
  height: 100%;
  width: 95%;
  z-index: 10;
  -ms-overflow-style: none;
  scrollbar-width: none;
  background-color: #fff;
  border-right: 2px solid gray;
  overflow-y: scroll;
}

.nav-container #navbar::-webkit-scrollbar {
  display: none;
}

.nav-container #navbar .nav-header {
  justify-content: center;
  align-items: center;
  padding: 20px 40px;
  display: flex;
}

.nav-container #navbar .nav-group {
  display: unset;
}

.nav-container #navbar .nav-group .nav-link {
  color: #0009;
  border-top: 1px solid gray;
  padding: 12px 40px;
  font-size: 1.1rem;
  text-decoration: none;
  display: block;
}

.nav-container #navbar .nav-group .nav-link:last-child {
  border-bottom: 1px solid gray;
}

.nav-container #navbar .nav-group .nav-link:hover {
  color: #fff;
  background-color: #007fff;
  transition: background-color 50ms;
}

#slider-tab {
  width: 25px;
  height: 100px;
  cursor: pointer;
  z-index: 9;
  border: 2px solid gray;
  border-radius: 0 15px 15px 0;
  flex-direction: column;
  top: 10%;
  left: calc(95% - 2px);
}

#slider-tab #tab-arrow {
  font-size: .7rem;
}

.return-ctnr {
  height: 60px;
  width: 90px;
  opacity: 1;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  right: 3vw;
}

.return-ctnr .return-top {
  color: #00f;
  font-size: .8rem;
}

.slideIn {
  opacity: 1;
  transform: translateY(0);
}

.slideOut {
  opacity: 0;
  transform: translateY(10vh);
}

.slideIn, .slideOut {
  transition: opacity 1s, transform 1s;
}

#main-doc, .main-section {
  flex-direction: column;
  display: flex;
}

#main-doc {
  height: 100%;
  gap: 20px;
  padding: 0 30px;
  transition: all 1s;
  position: fixed;
  top: 0;
  left: 350px;
  overflow-y: scroll;
}

#main-doc .main-section {
  gap: 20px;
}

#main-doc .main-section:last-child {
  margin-bottom: 60px;
}

#main-doc .main-section ul {
  margin-left: 40px;
}

#main-doc .main-section ul li, #main-doc .main-section ul li ul {
  margin-left: 20px;
}

@media (max-width: 1000px) {
  .nav-container {
    height: 250px;
    width: 100%;
    border-bottom: 2px solid #000;
    overflow-x: hidden;
    overflow-y: scroll;
    left: 0 !important;
  }

  .nav-container #navbar {
    border-right: none;
    width: 100% !important;
  }

  .nav-container #navbar .nav-group .nav-link:first-child {
    border-top: 1px solid #000;
  }

  .nav-container #navbar .nav-group .nav-link:last-child {
    border-bottom: none;
  }

  .nav-container #slider-tab {
    border: 2px solid #000;
  }

  #main-doc {
    height: calc(100% - 250px);
    width: 100%;
    top: 250px;
    left: 0 !important;
  }

  .return-ctnr {
    height: 30px;
  }
}

